import React, { useState } from "react";
import { format } from "date-fns";
import { enUS } from "date-fns/locale";
import { DatePickerCalendar } from "react-nice-dates";
import "react-nice-dates/build/style.css";
import DayTimePicker from "@mooncake-dev/react-day-time-picker";
import moment from "moment";

function Calendar({ setFormState, formState, onNext }) {
  const [date, setDate] = useState(null);
  const [isScheduling, setIsScheduling] = useState(false);
  const [isScheduled, setIsScheduled] = useState(false);
  const [scheduleErr, setScheduleErr] = useState("");
  const [pickedDay, setPickedDay] = useState(null);
  const [pickedTime, setPickedTime] = useState(null);

  const timeSlotValidator = (slotTime) => {
    const eveningTime = new Date(
      slotTime.getFullYear(),
      slotTime.getMonth(),
      slotTime.getDate(),
      19,
      0,
      0
    );
    const morningTime = new Date(
      slotTime.getFullYear(),
      slotTime.getMonth(),
      slotTime.getDate(),
      8,
      0,
      0
    );
    const isValid =
      slotTime.getTime() > morningTime.getTime() &&
      slotTime.getTime() < eveningTime.getTime();
    return isValid;
  };

  const fakeRequest = (data) => {
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        resolve({
          status: "ok",
          scheduled: data,
        });
      }, 2e3);
    });
  };

  const handleScheduled = (date) => {
    setFormState({
      ...formState,
      values: {
        ...formState.values,
        date: moment(date).format("dddd MMM Do, YYYY"),
        time: moment(date).format("hh:mm a"),
      },
    });
    onNext(1);
  };

  return (
    <div>
      <DayTimePicker
        timeSlotSizeMinutes={60}
        timeSlotValidator={timeSlotValidator}
        isLoading={isScheduling}
        isDone={isScheduled}
        err={scheduleErr}
        onConfirm={handleScheduled}
        confirmText="Next"
      />
    </div>
  );
}
export default Calendar;
