/* eslint-disable max-len */
import React, { useState } from "react";
import clsx from "clsx";
import PropTypes from "prop-types";
import moment from "moment";
import PerfectScrollbar from "react-perfect-scrollbar";
import {
  Box,
  Card,
  InputAdornment,
  SvgIcon,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  TextField,
  makeStyles,
  colors,
  Typography,
} from "@material-ui/core";
import { Search as SearchIcon } from "react-feather";

function applyFilters(events, users, query) {
  return events.filter((job) => {
    let matches = true;

    if (query && !job.name.toLowerCase().includes(query.toLowerCase())) {
      matches = false;
    }

    return matches;
  });
}

function applyPagination(events, page, limit) {
  return events.slice(page * limit, page * limit + limit);
}

const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: "transparent",
    boxShadow: "none",
  },
  bulkOperations: {
    position: "relative",
  },
  bulkActions: {
    paddingLeft: 4,
    paddingRight: 4,
    marginTop: 6,
    position: "absolute",
    width: "100%",
    zIndex: 2,
    backgroundColor: theme.palette.background.default,
  },
  bulkAction: {
    marginLeft: theme.spacing(2),
  },
  queryField: {
    width: 500,
  },
  categoryField: {
    flexBasis: 200,
  },
  availabilityField: {
    marginLeft: theme.spacing(2),
    flexBasis: 200,
  },
  stockField: {
    marginLeft: theme.spacing(2),
  },
  shippableField: {
    marginLeft: theme.spacing(2),
  },
  imageCell: {
    fontSize: 0,
    width: 68,
    flexBasis: 68,
    flexGrow: 0,
    flexShrink: 0,
  },
  image: {
    height: 68,
    width: 68,
  },
  pastDate: {
    color: colors.deepOrange[500],
  },
}));

function Events({ className, events, users, ...rest }) {
  const classes = useStyles();
  const today = moment(new Date()).format("MM/DD/YYYY");
  const [page, setPage] = useState(0);
  const [limit, setLimit] = useState(10);
  const [query, setQuery] = useState("");

  const handleQueryChange = (event) => {
    event.persist();
    setQuery(event.target.value);
  };

  const handlePageChange = (event, newPage) => {
    setPage(newPage);
  };

  const handleLimitChange = (event) => {
    setLimit(event.target.value);
  };

  const filteredJobs = applyFilters(events, users, query);
  const paginatedJobs = applyPagination(filteredJobs, page, limit);

  console.log(today);

  return (
    <Card className={clsx(classes.root, className)} {...rest}>
      <Box p={2}>
        <Box display="flex" alignItems="center">
          <TextField
            className={classes.queryField}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <SvgIcon fontSize="small" color="action">
                    <SearchIcon />
                  </SvgIcon>
                </InputAdornment>
              ),
            }}
            onChange={handleQueryChange}
            placeholder="Search by Name"
            value={query}
            variant="outlined"
          />
        </Box>
      </Box>
      <PerfectScrollbar>
        <Box minWidth={1200}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell padding="checkbox"></TableCell>
                <TableCell>Customer</TableCell>
                <TableCell>Date</TableCell>
                <TableCell>Time</TableCell>
                <TableCell>Type of Service</TableCell>
                <TableCell>Subject</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {paginatedJobs.map((job) => {
                return (
                  <TableRow hover key={job.id}>
                    <TableCell>
                      <Typography variant="body1">#{job.id}</Typography>
                    </TableCell>
                    <TableCell>
                      <Box display="flex" alignItems="center">
                        <div>
                          <Typography variant="h5">{job.name}</Typography>
                          <Typography variant="body2">{job.email}</Typography>
                        </div>
                      </Box>
                    </TableCell>
                    {today >
                    moment(job.date, "dddd MMM Do, YYYY").format(
                      "MM/DD/YYYY"
                    ) ? (
                      <TableCell className={classes.pastDate}>
                        {job.date}
                      </TableCell>
                    ) : (
                      <TableCell>{job.date}</TableCell>
                    )}
                    <TableCell>{job.time}</TableCell>
                    <TableCell>{job.service}</TableCell>
                    <TableCell>{job.subject}</TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
          <TablePagination
            component="div"
            count={filteredJobs.length}
            onChangePage={handlePageChange}
            onChangeRowsPerPage={handleLimitChange}
            page={page}
            rowsPerPage={limit}
            rowsPerPageOptions={[5, 10, 25]}
          />
        </Box>
      </PerfectScrollbar>
    </Card>
  );
}

Events.propTypes = {
  className: PropTypes.string,
  events: PropTypes.array,
};

Events.defaultProps = {
  events: [],
};

export default Events;
