import React, { useEffect, useState, useCallback } from "react";
import Page from "src/components/Page";
import { makeStyles } from "@material-ui/core/styles";
import { Grid } from "@material-ui/core";
// import HeroBackground from "src/components/HeroBackground";
// import Gallery from "./Gallery";
import { useStateValue } from "src/StateProvider";
import Uploader from "src/components/Uploader";
import SectionHeader from "src/components/SectionHeader";
import Section from "src/components/Section";
import clsx from "clsx";
import axios from "axios";
// import LoadingScreen from "src/components/LoadingScreen";
import ImagesList from "./ImagesList";

const useStyles = makeStyles((theme) => ({
  root: {
    height: "100%",
    width: "100%",
    overflow: "hidden",
    marginTop: 74,
  },
  section: {
    // padding: theme.spacing(10, 0),
    // maxWidth: "unset",
  },
}));

const WorkView = () => {
  const classes = useStyles();
  const [{ user }] = useStateValue();
  const [images, setImages] = useState(null);
  const [liked, setLiked] = useState(false);

  const getImages = useCallback(() => {
    axios.get("/photos").then((response) => {
      setImages(response.data);
    });

    return () => {
      setImages(null);
    };
  }, []);

  const handleLikes = (image, id) => {
    axios
      .put(`/photos/${id}`, {
        likes: image.likes + 1,
      })
      .then((response) => {
        setLiked(true);
      });
  };

  // const handleDelete = (id) => {
  //   axios
  //     .delete(`/api/v1/images/${id}`)
  //     .then((response) => {
  //       const newList = images.filter((image) => image.id !== id);
  //       setImages(newList);
  //     })
  //     .catch((error) => {
  //       const result = error;
  //       // set error if has any
  //       console.log(result);
  //     });
  // };

  useEffect(() => {
    getImages();
  }, [getImages, JSON.stringify(images), liked]);

  if (!images) {
    return null;
  }

  return (
    <Page title="Store" className={classes.root}>
      <Section className={clsx(classes.pagePaddingTop, classes.section)}>
        <SectionHeader title="Store" align="center" />
        <ImagesList images={images} handleLikes={handleLikes} liked={liked} />
      </Section>
    </Page>
  );
};

export default WorkView;
