import React from "react";
import PropTypes from "prop-types";
import * as Yup from "yup";
import { Formik } from "formik";
import {
  Box,
  Card,
  CardContent,
  FormHelperText,
  Grid,
  makeStyles,
  TextField,
} from "@material-ui/core";
import FilesDropzone from "src/components/FilesDropzone";
import { useSnackbar } from "notistack";

const useStyles = makeStyles(() => ({
  root: {},
  card: {
    backgroundColor: "transparent",
    boxShadow: "none",
    display: "flex",
    justifyContent: "center",
  },
  buttonCamera: {
    width: "100%",
  },
  cardItem: {
    width: "100%",
    // maxWidth: "300px",
    // marginTop: "2rem",
  },
}));

function Uploader({ setImages, images, text }) {
  const classes = useStyles();
  const { enqueueSnackbar } = useSnackbar();

  return (
    <Formik
      initialValues={{
        image: null,
        name: "",
        price: "",
        likes: 0,
        liked: false,
      }}
      validationSchema={Yup.object().shape({})}
      onSubmit={async (
        values,
        { setErrors, setStatus, setSubmitting, resetForm }
      ) => {
        try {
          const formData = new FormData();
          formData.append("image", values.image);
          formData.append("name", values.name);
          formData.append("price", values.price);
          formData.append("likes", values.likes);

          fetch("/photos.json", {
            method: "POST",
            body: formData,
          })
            .then((response) => {
              debugger;
              return response.json();
            })
            .then((data) => {
              // if (!images) {
              //   dispatch({
              //     type: "ADD_IMAGE",
              //     images: data,
              //   });
              // } else {
              setImages(images.concat(data));
              // }
            })
            .catch((error) => {
              debugger;
              console.log(error);
            });

          resetForm({});

          setStatus({ success: true });
          setSubmitting(false);
          enqueueSnackbar("Image Added", {
            variant: "success",
          });
        } catch (err) {
          setErrors({ submit: err.message });
          setStatus({ success: false });
          setSubmitting(false);
        }
      }}
    >
      {({
        errors,
        handleBlur,
        handleChange,
        handleSubmit,
        isSubmitting,
        setFieldValue,
        touched,
        values,
      }) => {
        return (
          <form onSubmit={handleSubmit} className={classes.root}>
            <Grid className={classes.buttonCamera}>
              <Card className={classes.card}>
                <Grid container>
                  <Grid item xs={12}>
                    <TextField
                      id="name"
                      name="name"
                      label="Name"
                      type="text"
                      value={values.name}
                      variant="outlined"
                      onChange={handleChange}
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <TextField
                      id="price"
                      name="price"
                      label="Price"
                      type="number"
                      value={values.price}
                      variant="outlined"
                      onChange={handleChange}
                    />
                  </Grid>
                </Grid>
                <CardContent className={classes.cardItem}>
                  <FilesDropzone
                    setFieldValue={setFieldValue}
                    handleSubmit={handleSubmit}
                    text={text}
                  />
                </CardContent>
              </Card>
            </Grid>
            {errors.submit && (
              <Box mt={3}>
                <FormHelperText error>{errors.submit}</FormHelperText>
              </Box>
            )}
          </form>
        );
      }}
    </Formik>
  );
}

Uploader.propTypes = {
  className: PropTypes.string,
};

export default Uploader;
